import React, { useState, useEffect, useCallback } from "react";
import Col from "react-bootstrap/Col";
import Card from "react-bootstrap/Card";
import Skeleton from "react-loading-skeleton";
import axios from "axios";

const ProjectCard = ({ value }) => {
  const {
    name,
    description,
    svn_url,
    stargazers_count,
    languages_url,
    pushed_at,
  } = value;
  return (
    <Col md={6}>
      <Card className="card shadow-lg p-3 mb-5 bg-white rounded">
        <Card.Body>
          <Card.Title as="h5">{name || <Skeleton />} </Card.Title>
          <Card.Text>
            {!description ? "" : description || <Skeleton count={3} />}{" "}
          </Card.Text>
          {svn_url ? <CardButtons svn_url={svn_url} /> : <Skeleton count={2} />}
          <hr />
          {languages_url ? (
            <Language languages_url={languages_url} repo_url={svn_url} />
          ) : (
            <Skeleton count={3} />
          )}
          {value ? (
            <CardFooter
              star_count={stargazers_count}
              repo_url={svn_url}
              pushed_at={pushed_at}
            />
          ) : (
            <Skeleton />
          )}
        </Card.Body>
      </Card>
    </Col>
  );
};

const CardButtons = ({ svn_url }) => {
  //define a switch case for different svn_url
  console.log(svn_url);
  let demo_url;

  if (svn_url === "https://github.com/syliow/pengin-nft-website") {
    demo_url = "https://penginclub-nft.netlify.app/";
    //second
  } else if (
    svn_url === "https://github.com/syliow/pengin-nft-minting-website"
  ) {
    demo_url = "https://pengin-nftmint.netlify.app/";
    //third
  } else if (svn_url === "https://github.com/syliow/crypto-price-tracker") {
    demo_url = "https://coin-pengin.netlify.app//";
    //fourth
  } else if (svn_url === "https://github.com/syliow/Volcanic-LandingPage") {
    demo_url = "https://volcanic-landingpage.netlify.app/";
    //fifth
  } else if (svn_url === "https://github.com/syliow/mdis-cafe") {
    demo_url = "https://mdis-cafe.netlify.app/";
  } else if (svn_url === "https://github.com/syliow/coinpengin-frontend") {
    demo_url = "https://coin-pengin.netlify.app/";
  } else if (svn_url === "https://github.com/syliow/coinpengin-backend") {
    demo_url = "https://crypto-price-tracker-backend.herokuapp.com/welcome";
  } else {
    svn_url = { svn_url };
  }

  return (
    <>
      <a
        href={demo_url}
        target=" _blank"
        className="btn btn-outline-secondary mr-3"
      >
        <i className="fas fa-eye" /> Demo
      </a>
      <a
        href={svn_url}
        target=" _blank"
        className="btn btn-outline-secondary mr-3"
      >
        <i className="fab fa-github" /> Repo
      </a>
    </>
  );
};

const Language = ({ languages_url, repo_url }) => {
  const [data, setData] = useState([]);

  const handleRequest = useCallback(async () => {
    try {
      const response = await axios.get(languages_url);
      return setData(response.data);
    } catch (error) {
      console.error(error.message);
    }
  }, [languages_url]);

  useEffect(() => {
    handleRequest();
  }, [handleRequest]);

  const array = [];
  let total_count = 0;
  for (let index in data) {
    array.push(index);
    total_count += data[index];
  }

  return (
    <div className="pb-3">
      Languages:{" "}
      {array.length
        ? array.map((language) => (
            <a
              key={language}
              className="badge badge-light card-link"
              href={repo_url + `/search?l=${language}`}
              target=" _blank"
            >
              {language}:{" "}
              {Math.trunc((data[language] / total_count) * 1000) / 10} %
            </a>
          ))
        : "code yet to be deployed."}
    </div>
  );
};

const CardFooter = ({ star_count, repo_url, pushed_at }) => {
  const [updated_at, setUpdated_at] = useState("0 mints");

  const handleUpdatetime = useCallback(() => {
    const date = new Date(pushed_at);
    const nowdate = new Date();
    const diff = nowdate.getTime() - date.getTime();
    const hours = Math.trunc(diff / 1000 / 60 / 60);

    if (hours < 24) {
      if (hours < 1) return setUpdated_at("just now");
      let measurement = hours === 1 ? "hour" : "hours";
      return setUpdated_at(`${hours.toString()} ${measurement} ago`);
    } else {
      const options = { day: "numeric", month: "long", year: "numeric" };
      const time = new Intl.DateTimeFormat("en-US", options).format(date);
      return setUpdated_at(`on ${time}`);
    }
  }, [pushed_at]);

  useEffect(() => {
    handleUpdatetime();
  }, [handleUpdatetime]);

  return (
    <p className="card-text">
      <a
        href={repo_url + "/stargazers"}
        target=" _blank"
        className="text-dark text-decoration-none"
      ></a>
      <small className="text-muted">Updated {updated_at}</small>
    </p>
  );
};

export default ProjectCard;
